@import "https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
}

body {
  min-height: 100vh;
  background: red;
  justify-content: center;
  align-items: center;
  display: flex;
}

a {
  color: gray;
  text-decoration: underline;
}

.container {
  width: 420px;
  background: #fff;
  border-radius: 7px;
  padding: 25px 28px 45px;
  box-shadow: 7px 7px 20px #0000000d;
}

.container h1 {
  text-align: center;
  margin-bottom: -20px;
  font-size: 28px;
  font-weight: 600;
}

.container .search {
  margin: 35px 0 18px;
  position: relative;
}

.search input {
  height: 53px;
  width: 100%;
  border: 1px solid gray;
  border-radius: 5px;
  outline: none;
  padding: 0 42px;
  font-size: 16px;
}

.search input:focus {
  border: 2px solid red;
  padding: 0 41px;
}

.search :where(i, span) {
  color: gray;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.search i {
  pointer-events: none;
  font-size: 16px;
  left: 18px;
}

.search input:focus ~ i, input:focus ~ span {
  color: red;
}

.search span {
  cursor: pointer;
  font-size: 18px;
  display: none;
  right: 15px;
}

.search input:valid ~ span {
  display: block;
}

.container .info-text {
  color: #000;
  text-align: center;
  margin: 0 0 -20px;
  font-size: 15px;
}

.container.active .info-text {
  display: none;
}

.info-text span {
  font-weight: 500;
}

.container ul {
  height: 0;
  opacity: 0;
  padding-right: 1px;
  transition: all .2s;
  overflow-y: hidden;
}

.container.active ul {
  opacity: 1;
  height: 303px;
}

.container ul li {
  border-bottom: 1px solid gray;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 14px;
  padding-bottom: 17px;
  list-style: none;
  display: flex;
}

ul li:last-child {
  border-bottom: 0;
  margin-bottom: 0;
  padding-bottom: 0;
}

ul .word p {
  color: red;
  font-size: 18px;
  font-weight: 500;
}

ul .word span {
  color: gray;
  font-size: 12px;
}

ul .word i {
  color: gray;
  cursor: pointer;
  font-size: 15px;
}

ul .content {
  max-height: 215px;
  overflow-y: auto;
}

ul .content::-webkit-scrollbar {
  width: 0;
}

.content li p {
  color: red;
  font-size: 17px;
  font-weight: 500;
}

.content li span {
  color: gray;
  font-size: 15px;
}

.content .synonyms .list {
  flex-wrap: wrap;
  display: flex;
}

.content .synonyms span {
  margin-right: 5px;
}

